import * as echarts from 'echarts'
import { getcase } from '@/network/api'

// draw the map of austrilia
export const drawmap = async (dom, v) => {
  let myChart = echarts.init(dom)
  let option
  let statemap = {
    'New South Wales': 'NSW',
    'Victoria': 'VIC',
    'Queensland': "QLD",
    'South Australia': 'SA',
    'Western Australia': 'WA',
    'Tasmania': 'TAS',
    'Northern Territory': 'NT',
    'Australian Capital Territory': 'ACT'
  }

  myChart.showLoading();
  //get the case and deaths data
  let result = await getcase();
  console.log(result)
  myChart.hideLoading();

  echarts.registerMap('AUS', v)

  // set option of the map
  
  option = {
    title: {
      text: 'AUS COVID-19 CASES AND DEATHS',
      subtext: `Last updated: ${new Date()}`,
      left: 'right'
    },
    tooltip: {
      trigger: 'item',
      showDelay: 0,
      transitionDuration: 0.2,
      formatter: function (params) {
        var value = (params.value + '').split('.');
        value = value[0].replace(/(\d{1,3})(?=(?:\d{3})+(?!\d))/g, '$1,');
        // return params.seriesName + '<br/>' + params.name + ': ' + value;
        // hover text showup
        return params.name + '<br/>' + 'Cases: ' + value + '<br/>' + 'Deaths: ' + result[`${statemap[params.name]}_Deaths`];
      }
    },
    visualMap: {
      left: 'right',
      min: 0,
      max: 20000,
      inRange: {
        color: ['lightskyblue', 'yellow', 'orangered']
        //color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026']
      },
      text: ['High', 'Low'],      
      calculable: true
    },
    toolbox: {
      show: true,
      //orient: 'vertical',
      left: 'left',
      top: 'top',
      // feature: {
      //   dataView: { readOnly: false },
      //   restore: {},
      //   saveAsImage: {}
      // }
    },
    series: [
      {
        name: 'AUS',
        type: 'map',
        roam: true,
        map: 'AUS',
        label: {
          show: true
        },
        emphasis: {
          label: {
            show: true
          }
        },
        // textFixed: {
        //   Alaska: [20, -20]
        // },
        data: [
          { name: 'New South Wales', value: result.NSW },
          { name: 'Victoria', value: result.VIC },
          { name: 'Queensland', value: result.QLD },
          { name: 'South Australia', value: result.SA },
          { name: 'Western Australia', value: result.WA },
          { name: 'Tasmania', value: result.TAS },
          { name: 'Northern Territory', value: result.NT },
          { name: 'Australian Capital Territory', value: result.ACT },
        ]
      }
    ]
  };

  myChart.setOption(option);

  option && myChart.setOption(option);
}